.change-section {
    overflow-x: hidden;
    overflow-y: auto;
    width: 280px;
  
    .label {
      @include task-sub-header();
      margin-bottom: 6px;
    }
  
    .change-box {
      input {
        margin: 4px 0 12px;
        width: 100%;
        background-color: #fafbfc;
        border: none;
        border-radius: 3px;
        box-shadow: inset 0 0 0 2px #dfe1e6;
        box-sizing: border-box;
        display: block;
        line-height: 20px;
        margin-bottom: 12px;
        outline: none;
        padding: 8px 12px;
        transition-duration: 85ms;
        transition-property: background-color, border-color, box-shadow;
        transition-timing-function: ease;
        color: #172b4d;
        font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue,
          sans-serif;
        font-size: 14px;
        font-weight: 400;
  
        &:focus {
          background-color: #fff;
          box-shadow: inset 0 0 0 2px #0079bf;
        }
      }
    }
  
    .change-box {
      margin-top: 12px;
  
      button {
        @include task-save();
        cursor: pointer;
        float: left;
        margin: 0 4px 0 0;
        background-color: #0079bf;
        padding-left: 24px;
        padding-right: 24px;
        color: #fff;
        border-radius: 3px;
      }
  
      .delete {
        background-color: #b04632;
        float: right;
        padding-left: 20px;
        padding-right: 20px;
      }
  
      .label {
        @include task-sub-header();
        margin-bottom: 6px;
      }
  
      .btn {
        @include button-link();
        text-align: center;
      }
  
      .colors-section {
        .box-container {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 0px;
          padding: 4px;
          margin: 0 -4px;
          overflow-x: hidden;
          grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  
          button {
            margin: 0;
            border-radius: 3px;
            width: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            box-shadow: none;
            height: 32px;
            cursor: pointer;
            border: none;
  
            float: left;
            height: 32px;
            margin: 0 8px 8px 0;
            padding: 0;
            width: 48px;
  
            &:hover {
              filter: saturate(85%) brightness(85%);
            }
  
            &:focus {
              &::after {
                content: '✓';
              }
            }
          }
        }
      }
    }
  }
  